.wrap{
  background:var(--beige);
  color:var(--ink);
  border:1px solid var(--lavender);
  border-radius:var(--radius);
}

/*  */
.header{
  display:flex; align-items:center; justify-content:space-between;
  padding:12px 14px;
  border-bottom:1px solid var(--lavender);
  background:rgba(0,0,0,.02);
}

.checkRow{
  display:flex; align-items:center; gap:8px; font-weight:700;
  cursor:pointer; /*   */
}
.dim{ color:#666; font-weight:400; }

/*   :    */
.right{
  display:flex; align-items:center; gap:8px;
  font-size:14px; /* = var(--control-font-size) */
}

/*  (  /   ) */
.ghost{
  height:32px;                         /* = var(--control-h) */
  border:1px solid var(--lavender);
  border-radius:10px;
  background:transparent;              /*  () */
  color:var(--ink);
  padding:0 10px;                      /* = var(--control-pad-x) */
  font-weight:400;                     /*   */
  font-size:14px;                      /*    */
  cursor:pointer;                      /*   */
}

/*  */
.list{ list-style:none; margin:0; padding:0; }
.empty{ padding:16px; color:#666; }

/*  */
.pager{
  display:flex; align-items:center; justify-content:center;
  gap:10px; padding:12px 14px;
  border-top:1px solid var(--lavender);
  background:rgba(0,0,0,.02);
}

.pageBtn{
  height:32px;
  border:1px solid var(--lavender);
  border-radius:10px;
  background:transparent;  /*   */
  color:var(--ink);
  padding:0 10px;
  font-weight:400;
  font-size:14px;
  cursor:pointer;           /*   */
}
.pageBtn:disabled{ opacity:.5; cursor:not-allowed; }
.pageInfo{ color:#333; font-weight:600; }

/* :      */
.sorterAtRight{ margin-right:6px; } /* 8px  6px */

/*   (    ) */
.moveWrap{ position:relative; }

/*   */
.moveMenu{
  position:absolute;
  right:0;
  top:calc(100% + 8px);
  background:#fff;
  border:1px solid var(--lavender);
  border-radius:10px;
  min-width:200px;
  z-index:40;
  box-shadow:0 8px 24px rgba(0,0,0,.08);
  overflow:hidden;
}

.moveHead{
  padding:8px 10px;
  font-weight:700;
  color:var(--ink);
  background:rgba(0,0,0,.02);
  border-bottom:1px solid var(--lavender);
}

.moveItem{
  width:100%;
  text-align:left;
  padding:8px 10px;
  background:#fff;
  border:none;
  border-bottom:1px solid rgba(0,0,0,.04);
  cursor:pointer;           /*   */
  color:var(--ink);
}
.moveItem:hover{ background:var(--footer-lavender, #efe7ff); }

.moveClose{
  width:100%;
  padding:8px 10px;
  background:#fff;
  border:none;
  cursor:pointer;           /*   */
  color:#625e5e;
}
