.sidebar{ display:flex; flex-direction:column; gap:16px; }

.group{
  background:var(--card);
  border:1px solid var(--line);
  border-radius:var(--radius);
  padding:14px;
}

/* =====  ===== */
.profileCard{
    display:flex;
    align-items:center;
    gap:10px;
    }
.avatarWrap{ display:flex; align-items:center; gap:10px; }
.avatar{
  width:56px; height:56px; border-radius:50%;
  object-fit:cover; border:1px solid var(--line);
}
.avatarPlaceholder{
  width:56px; height:56px; border-radius:50%;
  display:grid; place-items:center;
  border:1px dashed var(--line); color:#777; background:#fafafa;
  font-size:22px;
}
.changeBtn{
    width: 55px;
  border:1px solid var(--line); background:#fff; border-radius:10px;
  padding:6px 10px; font-size:13px; cursor:pointer;
}
.nick{
    width: 80px;
    font-weight:700;
    }

/* =====  ===== */
.albumHeader{ display:flex; align-items:center; justify-content:space-between; margin-bottom:8px; }
.iconBtn{
  width:28px;
  height:28px;
  border:none;
  border-radius:8px;
  background:none;
  color: rgb(179, 179, 179);
  font-style: normal;
  cursor: pointer;
  font-size: 14px;
}

.albumList{ display:flex; flex-direction:column; gap:6px; margin:0; padding:0; list-style:none; }
.albumItem{
  width:100%; text-align:left;
  border:1px solid var(--line);
  background:#fff; padding:11px 10px; border-radius:10px;
  cursor:pointer;
  transition: background .15s ease, border-color .15s ease, box-shadow .15s ease;
}
.albumItem:hover{ background:#f7f7f7; }

/*  / :  #c3b1d7,    */
:root{
  --album-active-border: #c3b1d7;                      /*     */
  --album-active-bg: var(--footer-lavender, #efe7ff);  /*  ( #efe7ff) */
}

/*   .active { outline:2px solid #111; }     */
.albumItem.active,
.albumItem:focus-visible{
  border-color: var(--album-active-border);
  background: var(--album-active-bg);
  outline: none;
  box-shadow:
    0 0 0 2px color-mix(in srgb, var(--album-active-border), transparent 70%) inset;
}

/*        */
.albumItem.active:hover{ background: var(--album-active-bg); }

.deleteAlbumBtn{
    height: 15px;
    position: absolute;
    left: 260px;
    margin-top: 10px;
    cursor: pointer;
    font-size: 14px;
    border: none;
    background: none;
    color: rgb(179, 179, 179);
    font-style: normal;
}
