
.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-header, .main-footer {
  flex-shrink: 0;
  background-color: #f1f0eb;
}

.main-container {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}


.main-content-area {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.music-player-area {
    position: fixed;
    right: 20px;
    top: 100px;
    z-index: 1000;
    margin: 20px 20px 20px -20px;
}
