/*   */
.page {
  min-height: 100vh;
  display: grid;
  place-items: center;
}

/*  :   + ( ) +   +    */
.card {
  /* /  */
  --ring-inset: 8px;    /*       */
  --line-inset: 12px;   /*    (=  ) */
  --pad: 28px;          /*   (// ) */

  width: 480px;
  max-width: 92vw;
}



/*     */
.card > * { position: relative; z-index: 3; }

/* /:   ,     */
.header {
  padding: var(--pad);
  display: flex;
  justify-content: center;
}
.title {
  margin: 0;
  font-size: 22px;
  font-weight: 800;
  color: #e0d8c5;
}



/* : /  ,  divider   */
.form {
  padding: 0 var(--pad) var(--pad);
  display: grid;
  gap: 14px;
}

.row { display: grid; gap: 6px; }
.label { font-size: 13px; color: #e0d8c5; visibility: hidden;}

/*   :    */
.input {
  width: calc(100% - 30px);      /*   ( 6px ) */
  margin: 0 auto;
  height: 42px;
  border-radius: 20px;     /*   */
  padding: 0 14px;
  background-color: #e0d8c5;
}



/*     */
input.input:-webkit-autofill,
input.input:-webkit-autofill:hover,
input.input:-webkit-autofill:focus {
  -webkit-text-fill-color: black;
  -webkit-box-shadow: 0 0 0 40px #e0d8c5 inset !important;
  box-shadow: 0 0 0 40px #e0d8c5 inset !important;
}








/*   */
.helper { font-size: 12px; color: #b7ad9b; }

/*     */
.submit {
  width: calc(100% - 12px);
  margin: 6px auto 0;
  height: 44px;
  border-radius: 999px;
  border: 1px solid #e0d8c5;
  background: #e0d8c5;
  color: #0e0e0e;
  font-weight: 700;
  cursor: pointer;

}
.submit:hover { filter: brightness(0.98); }

/* () */
.popupBackdrop {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,.35);
  display: grid;
  place-items: center;
  z-index: 1200;
}
.popupCard {
  width: 360px;
  max-width: 92vw;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 12px 40px rgba(0,0,0,.35);
  padding: 16px 16px 12px 16px;
}
.popupTitle { font-weight: 700; margin-bottom: 8px; color: #111; }
.popupBody { color: #333; margin-bottom: 14px; }
.popupActions { display: flex; justify-content: flex-end; }
.okBtn {
  background: #264e85;
  color: #fff;
  border: none;
  padding: 8px 14px;
  border-radius: 8px;
  cursor: pointer;
}
.okBtn:hover { filter: brightness(1.05); }
