/*      */
:root{
  --control-h: 30px;         /*   */
  --control-r: 10px;         /*   */
  --control-pad-x: 10px;     /*    */
  --control-font-size: 14px; /*    */
}

.sorter{
  position: relative;
  display: inline-block;
  height: var(--control-h);
  border: 1px solid var(--lavender);
  border-radius: var(--control-r);
  background: transparent;          /* : ( ) */
  min-width: 132px;                 /*     */
}

.select{
  position: absolute; inset: 0;
  appearance: none; -webkit-appearance: none; -moz-appearance: none;
  background: transparent;
  border: none; outline: none;
  color: var(--ink);                 /*   */
  height: 100%; width: 100%;
  padding: 0 calc(var(--control-pad-x) + 18px) 0 var(--control-pad-x); /*   */
  font-weight: 400;                  /*   */
  font-size: var(--control-font-size);
  line-height: var(--control-h);
  cursor: pointer;
}

/*    */
.sorter::after{
  content: "";
  position: absolute;
  right: 8px; top: 50%;
  width: 10px; height: 10px;
  transform: translateY(-50%);
  color: #625e5e;
  background: currentColor;
  -webkit-mask: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path fill="%23000" d="M3.2 6l4.8 4.8L12.8 6z"/></svg>') no-repeat center/100% 100%;
          mask: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path fill="%23000" d="M3.2 6l4.8 4.8L12.8 6z"/></svg>') no-repeat center/100% 100%;
  pointer-events: none;
}
