/*   */
.page {
  min-height: 100vh;
  display: grid;
  place-items: center;
}

/* :   (420px) +    */
.card {
  --pad: 24px;

  width: 420px;
  max-width: 90vw;
  position: relative;
}

/*  */
.header {
  padding: var(--pad);
  display: flex;
  justify-content: center;
}
.title {
  margin: 0;
  font-size: 24px;
  font-weight: 300;
  color: #e0d8c5;
}



/*  /  (  )  */
.tabs {
  position: relative;
  padding: 6px;
  margin: 0 var(--pad) 14px;           /*     */
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 6px;


          /*     */
  border-radius: 999px;                 /*  */

}

.tabBtn {
  appearance: none;
  border: none;
  height: 40px;
  border-radius: 999px;                 /* pill */
  background: #141414;                  /*   */
  color: #e0d8c5;                       /*   */
  font-weight: 800;
  letter-spacing: .2px;
  cursor: pointer;
  transition: background .2s ease, color .2s ease, transform .06s ease;
}
.tabBtn:hover { background: #1a1a1a; }
.tabBtn:active { transform: translateY(1px); }
.tabBtn:focus-visible {
  outline: 2px solid #e0d8c5;
  outline-offset: 2px;
}

/*  (/aria  ) */
.active,
.tabBtn[aria-selected="true"] {
  background: #e0d8c5;                  /*   */
  color: #0e0e0e;                       /*   */
  box-shadow: 0 1px 0 rgba(0,0,0,.25), inset 0 0 0 1px rgba(0,0,0,.05);
  border: 1px solid #e0d8c5;
}

/*  */
.fieldset {
  margin: 0 var(--pad) 18px var(--pad);
  border: 1px solid #6b6255;
  border-radius: 6px;
  padding: 14px 14px 10px;
}
.legend {
  padding: 0 8px;
  color: #e0d8c5;
  font-weight: 700;
}

/*  */
.form { display: grid; gap: 12px; margin-top: 6px; }
.row { display: grid; gap: 6px; }

/*   */
.label {
  font-size: 13px;
  visibility: hidden;
  padding-left: var(--label-indent);
}

/*  */
.input {
  width: calc(100% - 30px);
  margin: 0 auto;
  height: 40px;
  border-radius: 20px;
  border: 1px solid #5a5146;
  padding: 0 14px;
}
.input::placeholder { color: #b7ad9b; }
.input:focus { outline: 2px solid #e0d8c5; border-color: transparent; }

/*   */
.emailRow {
  display: grid;
  grid-template-columns: 1.3fr auto 1.0fr;
  gap: 8px;
  align-items: center;
  width: calc(100% - 2px);
  margin: 0 auto;
}
.at { color: #cfc6b4; font-size: 14px; }
.select {
  height: 40px;
  border-radius: 20px;
  border: 1px solid #5a5146;
  padding: 0 12px;
}

/* / */
.tip {
  font-size: 11px;
  color: #b7ad9b;
  padding-left: var(--label-indent);
  margin-top: -2px;
}

/*  () */
.actionsRow { display: flex; justify-content: center; margin-top: 4px; }
.smallBtn {
  height: 36px;
  padding: 0 16px;
  border-radius: 20px;
  border: 1px solid #e0d8c5;
  background: #e0d8c5;
  color: #0e0e0e;
  font-weight: 700;
  cursor: pointer;
}
.smallBtn:hover { filter: brightness(0.98); }

/*   */
.linkRow {
  padding: 10px var(--pad) var(--pad);
  display: flex;
  justify-content: center;
}
.linkBtn {
  background: transparent;
  border: none;
  color: #e0d8c5;
  text-decoration: underline;
  cursor: pointer;
  font-size: 13px;
}
.linkBtn:hover { opacity: .9; }

/*   ( ) */
.inlineActions {
  width: calc(100% - 30px);
  margin: 2px auto 0;
  display: flex;
  justify-content: flex-end;
}

