/* ===== :   ===== */
.modal * { box-sizing: border-box; }

/* =====  ===== */
.backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center;
  z-index: 1000;
}


/* =====   (  +   ) ===== */
.modal {
  /* ()    */
  --ring-inset: 8px;   /*       */
  --line-inset: 12px;  /*  /    */

  width: 420px;
  max-width: 90vw;
  color: #111;
  border: 1px solid #000;        /*    */
  border-radius: 12px;
  position: relative;
  box-shadow: 0 8px 28px rgba(0, 0, 0, 0.45);
  overflow: hidden;


  /*    */
  min-height: 520px;
  display: flex;
  flex-direction: column;
  background: #f1f0eb;
}

/*     */
.modal > * { position: relative; z-index: 3; }

/* =====  ===== */
.header {
  position: relative;
  padding: 16px 18px 8px 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.title {
  margin: 0;
  font-size: 22px;
  font-weight: 800;
  color: #000;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 22px;                     /*    */
}
.close {
  position: absolute;
  right: 18px;
  top: 16px;
  border: none;
  background: transparent;
  color: #333;
  font-size: 20px;
  cursor: pointer;
}

/*         */
.divider {
  margin: 41px 24px 0 24px;
}

/* =====  ===== */
.body {
  padding: 32px 24px 24px 24px;
  overflow: auto;
  flex: 1;
}

.row { margin-bottom: 12px; }
.label {
  font-size: 13px;
  margin-bottom: 6px;
  display: block;
  color: #000;
}

.input {
  width: 100%;
  height: 42px;
  border-radius: 6px;
  border: 1px solid #2a2a2a;
  background: #e0d8c5;
  color: #000;
  padding: 0 12px;
}
.input::placeholder { color: #6b6b6b; }
.input:focus { outline: 2px solid #000; border-color: transparent; }

.options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  color: #333;
  margin: 8px 0 14px;
}
.inline { display: inline-flex; align-items: center; gap: 6px; }

/*   */
.error {
  color: #c30000;
  font-size: 12px;
  margin-bottom: 10px;
}

/*  :  90%   */
.button {
  width: 90%;
  height: 44px;
  border-radius: 999px;
  border: 1px solid #000;
  background: #000;
  color: #f1f0eb;
  font-weight: 700;
  cursor: pointer;
  display: block;
  margin: 0 auto;
}

/*   :  / /  ( ) */
.actionsCol {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  margin: 10px 0 6px;
  font-size: 12px;
}

/*   */
.signupLink {
  color: #000;
  text-decoration: none;
  font-weight: 800;
}
.signupLink:hover { text-decoration: underline; }

.actionLink {
  color: #333;
  text-decoration: none;
}
.actionLink:hover { text-decoration: underline; }

/*    */
.dividerCenter {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  gap: 8px;
  margin: 14px 0;
  color: #555;
  font-size: 12px;
}
.dividerCenter::before,
.dividerCenter::after {
  content: "";
  height: 1px;
  background: #2a2a2a;
  display: block;
}

/* =====   ===== */
.socials {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 112px;
  margin-bottom: 12px;
}
.socialBtn {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  border: 1px solid #2a2a2a;
  background: #fff;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 20px;
  font-weight: 700;
}

/* /  */
.naver {
  background: #03c75a;
  color: #fff;
  border-color: #03a64d;
}
.kakao {
  background: #fee500;
  color: #381e1f;
  border-color: #d9c700;
}

/*     () */
.signupBtn {
  background: transparent;
  border: none;
  color: #000;
  font-weight: 800;
  font-size: 12px;
  cursor: pointer;
  padding: 0;
}
.signupBtn:hover { text-decoration: underline; }
