:root{
  --lavender:#e0d9e7;
  --beige:#f1f0eb;
  --ink:#111;
  --line:rgba(0,0,0,.12);
  --card:#fff;
  --radius:12px;
}

.page{
  min-height:100dvh;
  background:var(--beige);
  color:var(--ink);
}

.localHeader{ background:var(--lavender); border-bottom:1px solid var(--line); }
.headerInner{
  max-width:1100px;
  padding:20px 16px;
  display:flex;
  align-items:center;
  gap:12px;
  position:relative;
  margin-left: 25px;
  background: none;
}
.headset{
  flex:0 0 auto;
  background: none;
}
.title{
  font-size:28px;
  font-weight:700;
  background: none;
}
.sorter{ margin-left:auto; }

.layout{
  max-width:1080px;
  padding:0 16px;
  display:grid;
  grid-template-columns:260px 1fr;
  gap:24px;
  margin-left: 30px;
  margin-top: 24px;
}

@media (max-width:880px){
  .layout{ grid-template-columns:1fr; }
}
