/* check | cover | meta | actions */
.item{
  display:grid;
  grid-template-columns:32px 48px 1fr auto;
  align-items:center;
  gap:10px;
  padding:12px 14px;
  border-top:1px solid var(--lavender);
  color:var(--ink);
}
.item:first-child{ border-top:none; }
.check{ display:grid; place-items:center; }

/*  */
.cover{ width:48px; height:48px; display:grid; place-items:center; }
.coverImg{
  width:100%; height:100%; object-fit:cover;
  border-radius:8px; border:1px solid var(--lavender); background:#fff;
}
.coverPh{
  width:100%; height:100%; border-radius:8px;
  border:1px dashed var(--lavender);
  background:linear-gradient(135deg, rgba(224,217,231,.35), rgba(255,255,255,.6));
}

/*  */
.titleLine{ font-size:15px; }
.title{ font-weight:800; color:#625e5e; } /*   */
.artist{ color:#666; }
.sub{ color:#777; font-size:12px; margin-top:2px; }

/*   XEIcon     */
/* XEIcon    + ::before  */
.small i,
.small i::before { font-family: "xeicon" !important; }

/*  /  */
.small i { font-size:16px; line-height:1; display:block; color:#000; }
.small i::before { display:block; }

/* (  ) SVG    */
.small svg { width:16px; height:16px; display:block; color:#000; }


/*  */
.actions{ display:flex; gap:6px; }
.small{
  border:1px solid var(--lavender);
  background:#fff; color:var(--ink);
  border-radius:8px; padding:4px 8px;
}
/* () ON   */
.small[aria-pressed="true"] i { color: #ef4444; }

/* ()    */
.small i { transition: color .15s ease; }

