.main-section{
    height: auto;
    padding: 50px;
    position: relative;
    border-top: 0.5px solid black;
}

.main-container{
    display: flex;
    gap: 16px;
    text-align: flex-start;

}

.arrow-icon{
    position: absolute;
    left: 48px;
    font-size: 24px;
}

.album-container{
    display: flex;
    flex-direction: row;
    margin-top: 60px;
}

.album-cover-wrapper{
    position: relative;
    width: 350px;
    height: 350px;
    flex: 0 0 auto;
    margin-right: 10%;
    box-shadow: none;
    margin-bottom: 3%;
}

.album-background1,
.album-background2{
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.album-background1{
    background-color: #ffdab9;
    top: 20px;
    left: 20px;
    z-index: 1;
    box-shadow: 8px 8px 12px rgba(0, 0, 0, 0.4);
}

.album-background2{
    background-color: #bee3db;
    top: 10px;
    left: 10px;
    z-index: 2;
}

.album-image{
    width: 100%;
    height: 100%;
    overflow: hidden;
    box-shadow: none;
    z-index: 3;
    position: relative;
    border-radius: 10px;
}

.album-image img{
    width: 350px;
    height: 350px;
    object-fit: cover;
}

.album-info{
    display: flex;
    flex-direction: column;
    gap: 4px;
    flex: 1;
    align-items: flex-start;
    position: relative;
    margin-left: -80px;
}

.album-info h1{
    width: 330px;
    height: 80px;
    margin-top: -20px;
    text-align: left;
}

.album-info h3{
    position: absolute;
    margin-top: 120px;
    width: 220px;

}

.album-info p{
    cursor: pointer;
}

.user-profile{
    width: 100px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 5px 0;
    cursor: pointer;
}

.user-info{
    display: flex;
    flex-direction: row;
    position: absolute;
    bottom: 20px;
    align-items: flex-end;
}

.user-info p{
    margin-left: 20px;
}

.more-from-user{
    background-color: #e8c5e5;
    width: 70%;
    border: 1px solid #dbb4d8;
    border-radius: 10px;
    overflow: hidden;
    text-align: left;
    padding: 20px 80px;
    position: relative;
    clip-path: polygon(
        0 45px,
        45px 0,
        100% 0,
        100% 100%,
        0 100%
    ); 
}

.more-from-user::after {
    content: "";
    position: absolute;
    top: 0px;      
    left: 45px;
    width: 0;
    height: 0;
    border-top: 45px solid #e8c5e5; 
    border-left: 45px solid transparent;
    transform: rotate(90deg);
    transform-origin: top left;
    filter: drop-shadow(2px 1px 3px rgba(0, 0, 0, 0.5));
}

.more-from-user h2,
.more-from-user .song-card,
.more-from-user .song-card .song-title{
    background-color: #e8c5e5;
    width: 100%;
}

.more-from-user-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.87);
    padding-bottom: 10px;
}

.more-from-user h2{
    margin: 0;
    border-bottom: none;
    padding-bottom: 0;
}

.more-from-user span{
    font-weight: 300;
}

.user-song-list{
    display: flex;
    flex-direction: column;
    gap: 5px;
    background-color: #e8c5e5;
    margin-top: 10px;
}

.more-from-user .song-card{
    border-bottom: 0.5px solid rgba(119, 119, 119, 0.801);
    padding-bottom: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.more-from-user .song-card:last-child{
    padding-bottom: 0;
}

.more-from-user .song-card .song-title-link {
    flex: 1;
    text-align: left;
    text-decoration: none;
    color: inherit;
}

.song-card-buttons {
    display: flex;
    align-items: center;
    gap: 10px;
}

.more-from-user .song-card .plus-btn,
.more-from-user .song-card .delete-one-btn {
    border: none;
    background: none;
    padding: 4px;
    cursor: pointer;
    font-size: 16px;
    color: #555;
    transition: transform 0.2s;
}

.more-from-user .song-card .delete-one-btn {
    color: #ff4d4d;
}

.more-from-user .song-card .plus-btn:hover,
.more-from-user .song-card .delete-one-btn:hover {
    transform: scale(1.1);
}

.delete-all-btn {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    width: 110px;
}

.delete-all-btn:hover {
    background-color: #ff1a1a;
}

.content{
    width: 300px;
    margin-left: 340px;
    margin-top: -100px;
}

.button-group {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-top: 100px;
}

.button-group button{
    margin-top: 0;
}

.like-section {
    display: flex;
    align-items: center;
    gap: 8px;
}

.like-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 24px;
    color: #ccc;
    transition: color 0.2s;
    padding: 0;
}

.like-btn.liked {
    color: #ff4d4d;
}

.like-btn:hover {
    color: #ff4d4d;
}
