
.App {
  display: flex;
  flex-direction: column;
  min-height: 180vh;
}

.main-container {
  flex: 1;
}

.search-container {
  display: flex;
  align-items: center;
}

.search-container button {
  margin-right: 5px;
}

.main-footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.profile-link {
  display: flex;
  align-items: center;
  gap: 8px;
}

.profile-image {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  object-fit: cover;
}
