.search-container {
    position: absolute;
    top: -17px;
    right: 100%;
    opacity: 0;
    width: 300px;
    overflow: hidden;
    transition: opacity 0.3s ease-in-out;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    border-radius: 5px;
    z-index: 1000;
}

.search-container.visible{
    opacity: 1;
}

.search-bar {
    display: flex;
    justify-content: center;
    width: 100%;
}

.search-bar input {
    flex-grow: 1;
    height: 18px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: -2px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.3);
    background: none;
}

.search-results {
    width: 100%;
    max-height: 1000px;
    border-radius: 5px;
    padding: 5px 0;
    box-shadow: 0 4px 8px rgba(0,0,0,0.3);
}

.search-result-item {
    width:88%;
    display: flex;
    align-items: center;
    padding: 8px 10px;
    cursor: pointer;
    border-bottom: 1px solid rgb(173, 171, 171);
    background-color: #f1f0ebd2;
    margin-left: 8px;
    border-radius: 5px;
}

.search-result-item:last-child {
    border-bottom: none;
}

.search-result-item:hover {
    background-color: #d4d4d4bd;
}

.search-result-item img {
    width: 40px;
    height: 40px;
    margin-right: 10px;
    border-radius: 3px;
    object-fit: cover;
}

.song-info {
    display: flex;
    flex-direction: column;
}

.song-title {
    font-weight: bold;
    margin: 0;
    font-size: 0.9em;
    color: #333;
    margin-top: -15px;
}

.song-artist {
    color: #666;
    margin: 0;
    font-size: 0.8em;
    font-weight: 300;
}

.no-results {
    padding: 10px;
    color: #444;
    text-align: center;
    font-size: 0.9em;
    font-weight: 300;
}

.search-wrapper {
    position: relative; 
    display: flex;
    align-items: center;
}

.search-button{
    z-index: 10;
    border: none;
    font-size: 16px;
    text-align: center;
    width: 41.5px;
}
