@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,400..800;1,400..800&family=Exo+2:ital,wght@0,100..900;1,100..900&family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.cdnfonts.com/css/stencil');

body {
    background: #f1f0eb;
}

*{
    font-family: "Inter", sans-serif;
    font-optical-sizing: auto;
    font-weight: 100;
    font-style: italic;
    font-optical-sizing: auto;
}

.main-container{
    margin-left: -10px;
}

a{
    text-decoration: none;
    color: black;
}

.main{
    display: flex;
    flex-direction: column;
}

.main-header{
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #f1f0eb;
    font-weight: 300;
}

.main-header .title{
    margin-left: 35px;
}

.main-header .nav_bar{
    display: flex;
    margin-right: 50px;
    gap: 30px;
    align-items: center;
}

.main-header .nav_bar a{
    text-Decoration: none;
    color: inherit;
}

.login-button {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    color: inherit;
    cursor: pointer;
    font-size: 1em;
}

.main-content{
    display: flex;
    flex-direction: column;
}

.contentbest, .contenthot, .content1, .content2, .content3, .content4{
    width: border-box;
    height: 530px;
    margin-left: -10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.contentbest h2, .contenthot h2, .content1 h2, .content2 h2, .content3 h2, .content4 h2{
    margin-left: 50px;
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 90px;
    background: none;
    font-optical-sizing: auto;
}

.content1 h2{
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
}

.content2 h2{
    font-family: 'Stencil', sans-serif;
}

.content3 h2{
    font-family: "EB Garamond", serif;
}

.content4 h2{
    font-family: "Exo 2", sans-serif;
}

.contentbest-music,
.contenthot-music,
.content1-music,
.content2-music,
.content3-music,
.content4-music{
    display: flex;
    gap: 50px;
    margin-left: 50px;
    background: none;
}

.music-item{
    width:230px;
    height: 290px;
    border-radius: 15px;
    box-shadow: 0 10px 10px rgba(0,0,0,0.5);
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    background: #f1f0eb;
}

.music-item:hover{
    margin-top: 5px;
    transition: all 0.3s ease-in-out;
    box-shadow: 0 0px 0px rgba(0,0,0,0.5);
}

.music-item-thumbnail{
    position: relative;
    width: 230px;
    height: 260px;
    border-radius: 20px;
    margin-bottom: -50px;
}

.music-item-thumbnail img{
    width: 180px;
    height: 180px;
    margin-left: 25px;
    object-fit: cover;
    border-radius: 15px;
    transition: filter 0.3s ease-in-out;
    margin-top: 20px;
}

.music-item-thumbnail:hover img{
    filter: blur(2px) brightness(0.7);
}

.music-item-buttons {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    gap: 20px;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.music-item-thumbnail:hover .music-item-buttons {
    opacity: 1;
}

.music-item-buttons button,
.music-item-buttons a {
    background: rgba(255, 255, 255, 0.8);
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: #333;
    font-size: 18px;
    transition: background-color 0.3s;
}

.music-item-buttons button:hover,
.music-item-buttons a:hover {
    background: #fff;
}


.music-item-title{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    width: 200px;
    height: 30px;
    margin-top: 5px;
    margin-left: 15px;
    text-align: center;
    font-weight: 400;
    text-decoration: none;
    color: black;
    z-index: 10;
}

.music-item-artist {
    width: 230px;
    height: 30px;
    margin-top: -20px;
    font-size: 14px;
    text-align: center;
    font-weight: 200;
}

.music-item-genre{
    width: 230px;
    height: 30px;
    margin-top: -30px;
    font-size: 12px;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contentbest{
    background: #c45d5d;
}

.contenthot{
    background: #cf8e6b;
}

.content1{
    background: #79b8ac;
}

.content2{
    background: #bec0ba;
}

.content3{
    background: #b89eb5;
}

.content4{
    background: #c07da8;
}

.login-prompt{
    text-align: center;
    margin-top: 150px;
    font-size: 24px;
    margin-bottom: 320px;
}

.recommend, .best, .pop, .hiphop, .acoustic, .electronic{
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    color: inherit;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.recommend:hover{
    color: #ff0000;
    transition: all 0.3s ease-in-out;
}

.best:hover{
    color: #ff5900;
    transition: all 0.3s ease-in-out;
}

.pop:hover{
    color: #02c29b;
    transition: all 0.3s ease-in-out;
}

.hiphop:hover{
    color: #a3a0a0;
    transition: all 0.3s ease-in-out;
}

.acoustic:hover{
    color: #d140c5;
    transition: all 0.3s ease-in-out;
}

.electronic:hover{
    color: #ff00a2;
    transition: all 0.3s ease-in-out;
}

.music-item-buttons button.liked {
    color: #ff4d4d;
}
