.comment-wrapper {
    width: 1050px;
    padding: 30px;
    border-radius: 24px;
    border: none;
    background: linear-gradient(145deg, #ffffff 0%, #f8f9ff 100%);
    box-shadow: 0 8px 32px rgba(106, 99, 248, 0.15);
    position: relative;
    overflow: visible;
    margin-bottom: 50px;
}

.comment-title {
    font-size: 22px;
    font-weight: 800;
    margin-bottom: 20px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.comment-input-wrap {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

.comment-input {
    flex: 1;
    padding: 14px 16px;
    border-radius: 12px;
    border: 2px solid #e8e8f5;
    background: #fff;
    font-size: 14px;
    transition: all 0.3s ease;
    outline: none;
}

.comment-input:focus {
    border-color: #6a63f8;
    box-shadow: 0 0 0 4px rgba(106, 99, 248, 0.1);
}

.comment-button {
    padding: 14px 24px;
    border: none;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border-radius: 12px;
    cursor: pointer;
    font-weight: 600;
    font-size: 14px;
    transition: all 0.3s ease;
    box-shadow: 0 4px 12px rgba(106, 99, 248, 0.3);
}

.comment-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(106, 99, 248, 0.4);
}

.comment-button:active {
    transform: translateY(0);
}

.comment-item {
    padding: 18px;
    background: #fff;
    border-radius: 16px;
    border: 2px solid #f0f0f8;
    margin-bottom: 14px;
    transition: all 0.3s ease;
}

.comment-item:hover {
    border-color: #e0e0f0;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
}

.comment-item b {
    color: #764ba2;
    font-size: 15px;
}

.comment-item p {
    margin: 8px 0;
    color: #333;
    line-height: 1.5;
}

.like-button {
    background: transparent;
    border: none;
    font-size: 16px;
    cursor: pointer;
    margin-top: 6px;
    margin-right: 10px;
    transition: transform 0.2s ease, color 0.2s ease;
    color: #999;
}

.like-button:hover {
    transform: scale(1.15);
}

.like-button.liked {
    color: #ff5b82;
}

.reply-toggle-button {
    background: transparent;
    border: none;
    color: #6a63f8;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
    margin-top: 6px;
    transition: all 0.2s ease;
}

.reply-toggle-button:hover {
    color: #764ba2;
    transform: translateX(3px);
}

.reply-input-wrap {
    display: flex;
    gap: 8px;
    margin-top: 12px;
    margin-left: 25px;
}

.reply-input {
    flex: 1;
    padding: 10px 14px;
    border-radius: 10px;
    border: 2px solid #e8e8f5;
    background: #fff;
    font-size: 13px;
    transition: all 0.3s ease;
    outline: none;
}

.reply-input:focus {
    border-color: #b8b2ff;
    box-shadow: 0 0 0 3px rgba(184, 178, 255, 0.15);
}

.reply-submit {
    padding: 10px 18px;
    background: linear-gradient(135deg, #b8b2ff 0%, #9b8fff 100%);
    border-radius: 10px;
    border: none;
    color: white;
    cursor: pointer;
    font-weight: 600;
    font-size: 13px;
    transition: all 0.3s ease;
    box-shadow: 0 3px 10px rgba(184, 178, 255, 0.3);
}

.reply-submit:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(184, 178, 255, 0.4);
}

.reply-container {
    margin-left: 40px;
    margin-top: 12px;
    padding: 14px;
    background: linear-gradient(145deg, #faf9ff 0%, #f3eaff 100%);
    border-radius: 14px;
    border: 2px solid #e8dcff;
    position: relative;
    transition: all 0.3s ease;
}

.reply-container:hover {
    border-color: #d7c9ff;
    box-shadow: 0 3px 12px rgba(138, 120, 255, 0.1);
}

.reply-container b {
    color: #8a78ff;
    font-size: 14px;
}

.reply-arrow {
    position: absolute;
    left: -22px;
    top: 18px;
    font-size: 20px;
    color: #8a78ff;
}

.pagination {
    margin-top: 24px;
    display: flex;
    justify-content: center;
    gap: 8px;
    height: 30px;
}

.page-number,
.page-arrow {
    padding: 8px 14px;
    border: 2px solid #e8e8f5;
    border-radius: 10px;
    cursor: pointer;
    background: #fff;
    font-weight: 600;
    color: #666;
    transition: all 0.3s ease;
    height: 20px;
}

.page-number{
    margin-top: 500px;

}

.page-number:hover,
.page-arrow:hover {
    border-color: #6a63f8;
    color: #6a63f8;
    transform: translateY(-2px);
}

.page-active {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border-color: transparent;
    box-shadow: 0 4px 12px rgba(106, 99, 248, 0.3);
}

.notification {
    position: absolute;
    top: -60px;
    left: 50%;
    transform: translateX(-50%);
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: #fff;
    padding: 14px 28px;
    border-radius: 20px;
    font-size: 15px;
    font-weight: 600;
    box-shadow: 0 4px 20px rgba(102, 126, 234, 0.4);
    z-index: 9999;
    border: 2px solid rgba(255, 255, 255, 0.3);
    animation: slideDown 0.3s ease-out;
    white-space: nowrap;
}

.comment-header,
.reply-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}

.comment-user {
    display: flex;
    align-items: center;
    gap: 10px;
}

.comment-plan {
    display: inline-flex;
    align-items: center;
    margin-left: 6px;
    font-weight: 700;
    font-size: 14px;
}

.plan-icon {
    font-size: 14px;
}

.plan-label {
    font-size: 12px;
    color: #5a4fb7;
}

.comment-avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #e0e0f0;
}

.comment-avatar.placeholder {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: #d8d6f5;
    color: #4a3fa0;
    font-weight: 700;
    font-size: 14px;
}

.comment-role {
    display: inline-block;
    margin-left: 8px;
    padding: 2px 8px;
    border-radius: 12px;
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    background: #f0f0f8;
    color: #666;
}

.comment-role.admin {
    background: rgba(255, 93, 135, 0.15);
    color: #ff5d87;
}

.comment-role.user {
    background: rgba(118, 75, 162, 0.15);
    color: #764ba2;
}

.reply-like {
    color: #8a78ff;
}

.reply-like.liked {
    color: #b08cff;
}

@keyframes slideDown {
    0% {
        opacity: 0;
        transform: translateX(-50%) translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateX(-50%) translateY(0);
    }
}
