:root {
  --grid-gap: 18px;
}


.wrap {
  display: grid;
  grid-template-columns: 1fr 320px;
  gap: var(--grid-gap);
  margin-left: 50px;
}


.titleRow {
  display:flex;
  align-items:center;
  gap:12px;
  margin:16px 0 12px;
}
.back {
  display:inline-flex;
  cursor:pointer;
  transform: translateY(-6px);
}
.back i { font-size: 22px; line-height: 1; }


.h1{
  font-family: "DM Serif Display", serif;
  font-style: italic;
  font-size: 56px;
  font-weight: 900;
  letter-spacing: 1.5px;
  margin: 0;
}

/* ================================
     ( )
   ================================ */

.listCard{

  --radius: 12px;
  --frame: 12px;
  --frame-color: #dadcd4;
  --fill: #cad9e6;

  position: relative;
  margin-top: 28px;
  border: none;
  border-radius: var(--radius);
  background: var(--frame-color);
  overflow: hidden;


  box-shadow:
    8px 10px 24px rgba(0,0,0,0.16),
    2px  3px  8px rgba(0,0,0,0.08);
}


.listCard::before{
  content:"";
  position:absolute;
  border-radius: calc(var(--radius) - 4px);
  background: var(--fill);
  z-index:0;
}


.listCard > * { position: relative; z-index: 1; }


.listCard::after{ content:none !important; }

/* ================================
    
   ================================ */
.row {
  display: grid;
  grid-template-columns: 65px 1fr 126px 80px auto;
  align-items:center;
  gap:11px;
  padding:14px 11px;
  border-bottom:1px solid rgba(0,0,0,.08);
  transition: background .2s;
  background-color: #f1f0eb;
}
.row:last-child { border-bottom:none; }
.row:hover { background: rgba(255,255,255,.35); }

.idx {
  font-weight:700;
  color:#0f3a5d;
  font-size:.9em;
}

.coverTitle {
  display:grid;
  grid-template-columns:50px 1fr;
  align-items:center;
  gap:11px;
  overflow:hidden;
}
.cover {
  width:50px;
  height:50px;
  border-radius:5px;
  object-fit:cover;
  transition:transform .18s;
  transform-origin:left center;
  box-shadow:0 1px 0 rgba(0,0,0,.2);
}
.title {
  font-size:18px;
  font-weight:800;
  transition:transform .18s;
  transform-origin:left center;
  white-space:nowrap;
  overflow:hidden;
  text-overflow:ellipsis;
}

.nick {
  color:#3b556e;
  justify-self:end;
  font-size:.9em;
}

.likeCount {
  display: flex;
  align-items: center;
  gap: 5px;
  color: #ff4d4d;
  font-size: 0.9em;
  justify-self: center;
}


.addBtn {
  justify-self:end;
  width:29px;
  height:29px;
  border-radius:50%;
  border:none;
  background:#dadcd4;
  display:grid;
  place-items:center;
  cursor:pointer;
  font-weight:700;
}
.addBtn svg { width:14px; height:14px; }
.addBtn:hover { filter: brightness(0.97); }


.row:hover .cover { transform: scale(1.08); }
.row:hover .title { transform: scale(1.06); }

/* ================================
   Pagination
   ================================ */
.pager{
  display:flex;
  justify-content:center;
  align-items:center;
  gap:7px;
  margin-top:12px;
}

.pageBtn{
  min-width:32px;
  height:32px;
  padding:0 10px;
  border-radius:999px;
  border:none;
  background: var(--page-bg);
  color:#000;
  font-weight:700;
  cursor:pointer;
}
.pageBtn:disabled{ opacity:.4; cursor:not-allowed; }

.pageBtnActive{
  background: var(--page-bg);
  color:#000;
  text-decoration: underline;
}

/* ================================
       
   ================================ */

.compact {
  width: 94%;
  margin-right: auto;
  box-sizing: border-box;
}

.compact .row {
  grid-template-columns: 65px 1fr 200px 80px auto;
  gap: 9px;
  padding: 20px 30px;
}

.compact .coverTitle {
  grid-template-columns: 46px 1fr;
  gap: 9px;
  background: none;
}
.compact .cover {
  width: 46px;
  height: 46px;
  border-radius: 4px;
}

.compact .title {
    font-size: 16px;
    background: none;
}

.compact .idx,
.compact .nick {
    font-size: 0.88em;
    background: none;
}

.compact .addBtn {
    width: 26px; height: 26px;
}
.compact .addBtn svg { width: 12px; height: 12px; }

.buttonGroup {
  display: flex;
  align-items: center;
  gap: 8px;
}

.compact .idx{
    width: 200px;
}
