.upload-container {
    width: 100%;
    max-width: 600px;
    margin: 50px auto;
    padding: 40px;
    background-color: #cccbc8;
    border-radius: 20px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
    box-sizing: border-box;
}

.upload-container h1 {
    text-align: center;
    font-size: 28px;
    font-weight: 600;
    font-style: normal;
    color: #333;
    background: none;
    margin-bottom: 30px;
}

.upload-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    background: none;
}

.form-group {
    display: flex;
    flex-direction: column;
    background: none;
}

.form-group label {
    font-size: 14px;
    font-weight: 500;
    font-style: normal;
    color: #555;
    margin-bottom: 8px;
    background: none;
}

.form-group input[type="text"],
.form-group textarea {
    padding: 12px;
    border: 1px solid #DDD;
    border-radius: 8px;
    font-size: 16px;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    background-color: #f1f0eb;
    transition: border-color 0.2s, box-shadow 0.2s;
}

.form-group input[type="text"]:focus,
.form-group textarea:focus {
    outline: none;
    border-color: #FF6B6B;
    box-shadow: 0 0 0 3px rgba(255, 107, 107, 0.2);
}

.form-group textarea {
    min-height: 120px;
    resize: vertical;
}

.form-group input[type="file"] {
    font-size: 14px;
    font-style: normal;
    background: none;
}

.submit-btn {
    padding: 15px;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s;
    margin-top: 10px;
}

.submit-btn:hover {
    background-color: #FF6B6B;
}
