.inquiry-detail-container * {
    font-weight: 300;
    font-style: normal;
    font-family: "Pretendard", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, "Noto Sans KR", sans-serif;
}

.inquiry-line{
    width: 100%;
    border-top: 1px solid black;
}

.inquiry-detail-container{
    display: flex;
    flex-direction: column;
    width: 70%;
    margin-left: 20px;
    padding: 0 35px;
    gap: 10px;
}

.inquiry-detail-container h1{
    text-align: center;
}

.board-header{
    display: flex;
    justify-content: space-between;
}

/*   */

.board-wrapper{
    border-top: 1px solid #c9c9c9;
    padding: 20px 35px;
}

.board-title{
    border-bottom: 1px solid black;
}

.board-content {
    display: grid;
    grid-template-columns: 3fr 1fr;
    gap: 10px;
    align-items: stretch; 
    min-height: 250px;
    margin-bottom: 20px;

}

.board-write{
    font-size: 1rem;
    white-space: pre-wrap;
    line-height: 1.6;
    word-break: break-word; 
    display: flex;
    align-items: flex-start; 
    justify-content: flex-start;
}

.board-file{
    justify-self: center;
    align-self: end;   
    font-size: 0.875rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
}

.board-file h4{
    margin: 0;
}

.board-file .file-item .file-clip {
    font-size: 0.8rem; 
    color: #555;
}

.board-file .file-item a{
    text-decoration: none;
    color: #444;
}

.board-file a:hover {
    text-decoration: underline;
    color: #8770d1;
}

.detail-admin-answer {
    border-top: 1px solid black;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 15px 0 0 0;
    margin-bottom: 50px;
}

.detail-admin-answer p {
    margin: 0;
    font-size: 1rem;
    line-height: 1.6;
    white-space: pre-wrap;
    word-break: break-word;
    max-width: 640px;
}

/*   */

.board-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px; 
}

.board-footer button {
    border: none; 
    border-radius: 10px;
    background-color: #dcd3ff;
    padding: 6px 14px;
    font-size: 0.95rem;
    cursor: pointer;
    transition: all 0.15s ease;
    box-shadow: 1px 1.5px 2px rgba(0,0,0,0.15);
    font-weight: 400;
}

.board-footer button:hover {
    background-color: #d3c5ff;
    transform: translateY(-1px);
}

/*   */

/*   */
.custom-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

/*   */
.custom-modal {
    padding: 20px 30px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 3px 8px rgba(0,0,0,0.2);
    min-width: 280px;
}

/*   */
.modal-buttons {
    margin-top: 15px;
    display: flex;
    justify-content: center;
    gap: 10px;
}

.modal-buttons button {
    padding: 6px 12px;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    background-color: #dcd3ff;
    transition: all 0.15s ease;
}

.modal-buttons button:hover {
    background-color: #cbbfff;
}
